import { S3Client, ListObjectsV2Command } from "@aws-sdk/client-s3";
import { auth } from "@/lib/auth";

export async function GET(req) {
  try {
    const session = await auth();

    if (!session || !session.user) {
      return Response.json({ error: "Unauthorized" }, { status: 401 });
    }

    const userId = session.user.id || session.user.email; // User's folder in S3

    // AWS S3 Configuration
    const s3 = new S3Client({
      region: process.env.AWS_REGION,
      credentials: {
        accessKeyId: process.env.AWS_ACCESS_KEY_ID,
        secretAccessKey: process.env.AWS_SECRET_ACCESS_KEY,
      },
    });

    // Get list of files
    const params = {
      Bucket: process.env.AWS_BUCKET_NAME,
      Prefix: `${userId}/`, // Fetch files under user folder
    };

    const { Contents } = await s3.send(new ListObjectsV2Command(params));

    if (!Contents) {
      return Response.json({ files: [] }, { status: 200 });
    }

    // Extract file URLs and keys
    const files = Contents.map((file) => ({
      key: file.Key,
      url: `https://${process.env.AWS_BUCKET_NAME}.s3.${process.env.AWS_REGION}.amazonaws.com/${file.Key}`,
    }));

    return Response.json({ files }, { status: 200 });
  } catch (error) {
    console.error("Fetch Files Error:", error);
    return Response.json({ error: "Failed to fetch files" }, { status: 500 });
  }
}
